<template>
    <div id="star-trending">
        <apexchart type='line' :options='options' :series='series' />
    </div>
</template>

<script>
import Vue from 'vue'
import VueApexCharts from 'vue-apexcharts'
import _ from 'lodash'
import moment from 'moment'
import v3 from '../../github/api/v3'

Vue.use(VueApexCharts)
Vue.component('apexchart', VueApexCharts)

function rangeInDate(start, end) {
    const days = [];
    for (var i = new Date(start.getTime()); i < end;) {
        days.push(new Date(i.getTime()));
        i.setDate(i.getDate() + 1)
    }
    days.push(end);
    return days;
};

export default {
    name: 'StarTrendingChart',
    data: () => {
        return {
            options: {
                theme: {
                    mode: 'light',
                    monochrome: {
                        colors: '#333',
                    },
                },
                chart: {
                    height: window.innerHeight - 20,
                },
                stroke: {
                    width: 2,
                    colors: ['#333'],
                },
                markers: {
                    colors: ['#333'],
                    size: 4,
                    hover: {
                        size: 8,
                    }
                },
                dataLabels: {
                    style: {
                        colors: ['#333'],
                    },
                },
                xaxis: {
                    type: 'category',
                    labels: {
                        //rotate: -45,
                        //rotateAlways: true,
                    },
                    categories: [moment().format('MM-DD')],
                },
                yaxis: {
                    tickAmount: 10,
                    title: {
                        text: 'Stars',
                    },
                },
            },
            series: [{
                name: 'Stars',
                data: [0],
            }],
        };
    },
    mounted() {
        this.updateChart();
    },
    methods: {
        async updateChart() {
            const repo = await v3.fetchRepository(this.$route.path)
            if (!(repo && repo.created_at)) {
                return;
            }

            const createdAt = new Date(moment(repo.created_at).format('MM-DD-YYYY'));
            const today = new Date(moment().format('MM-DD-YYYY'));
            const days = _.map(rangeInDate(createdAt, today), it => moment(it).format('YYYY-MM-DD'));
            const stargazers = await v3.fetchStargazers(this.$route.path);
            const stars = _.groupBy(_.map(stargazers, it => {
                return {
                    starred_at: it.starred_at.substr(0, 10)
                };
            }), it => it.starred_at)
            const daily = _.map(days, date => {
                return {
                    date: date,
                    star: (stars[date] || []).length,
                };
            });
            const labels = _.map(daily, it => `${it.date.substring(5,7)}/${it.date.substring(8,10)}`);
            const dataset = _.reduce(_.map(daily, 'star'), (acc, num) => {
                acc.push((acc.length > 0 ? _.last(acc) : 0) + num)
                return acc;
            }, []);
            this.options = {
                theme: {
                    mode: 'light',
                    monochrome: {
                        colors: '#333',
                    },
                },
                chart: {
                    height: window.innerHeight - 20,
                },
                stroke: {
                    width: 2,
                    colors: ['#333'],
                },
                markers: {
                    colors: ['#333'],
                    size: 4,
                    hover: {
                        size: 8,
                    }
                },
                dataLabels: {
                    style: {
                        colors: ['#333'],
                    },
                },
                xaxis: {
                    type: 'category',
                    labels: {
                        //rotate: -45,
                        //rotateAlways: true,
                    },
                    categories: labels,
                    title: {
                        text: `https://github.com/${this.$route.path.replace(/^\//, '')}`,
                    },
                },
                yaxis: {
                    tickAmount: 10,
                    title: {
                        text: 'Stars',
                    },
                },
            };
            this.series = [{
                data: dataset,
            }];
        },
    },
}
</script>

<style>
.apexcharts-tooltip-marker {
    background-color: #333!important;
}
</style>
<style scoped>
#star-trending {
    width: 100%;
    height: 100%;
    position: relative;
}
</style>
